
import React, {useEffect} from 'react';
import LightSpeed from 'react-reveal/LightSpeed';
import Fade from 'react-reveal/Fade';
import Header from '../comp/Header';
import { FullPage, Slide } from 'react-full-page';

export default function About(){

    useEffect(()=>{
        typing()
    })

    function typing(){
        var typingBool = false;
        var typingIndex = 0;
        var typingTxt = '센스있는';
        const typing = document.querySelector('.typing');

        typingTxt = typingTxt.split('');

        if(typingBool == false){
            typingBool = true;

            typing.innerHTML = '';
            var tyInt = setInterval(type,300);
        }

        function type(){
            if(typingIndex < typingTxt.length){
                document.querySelector('.typing').append(typingTxt[typingIndex])
                typingIndex++;
            }else{
                clearInterval(tyInt);
            }
        }
    }

    return(
        <div>
        <Header color={'#111'}/>

        <FullPage>

        <Slide>
        <div className="line sec1">
            <div>
                <div className="title">
                    안녕하세요.<br/>
                    <div className="source" style={{display:"none"}}>센스있는</div>
                    <div className="typing"></div>
                    <strong>프론트엔드 개발자</strong><br/>
                    필요하신가요?
                </div>
                <div className="gif">
                    <img src="./img/icon/about-mimo1.png" className="mimo" alt="미모티콘 이미지"></img>
                </div>
            </div>

            <svg class="arrow-down">
                <path class="a1" d="M0 0 L15 16 L30 0"></path>
                <path class="a2" d="M0 10 L15 26 L30 10"></path>
            </svg>
        </div>
        </Slide>

        <Slide>
        <div className="line sec2">
            <div>
                <div className="title">
                    <LightSpeed left cascade triggerOnce={true}>
                    <Fade left><span>알아서, <b className="c">잘</b></span></Fade>
                    <Fade left delay={500}><span><b className="c">꼼꼼</b>하게</span></Fade>
                    <Fade left delay={1000}><span>누구보다 <b className="c">열심히</b></span></Fade>
                    </LightSpeed>

                    <Fade left delay={1500}>
                    <small style={{marginTop:"30%"}}><b>이런 사원을 찾으신다면</b></small>
                    <small>제 포트폴리오에 <br/><b style={{borderBottom:"2px solid #111"}}>시간 내주실 가치는 충분한 것 같아요! 🧏‍♀️</b></small>
                    </Fade>
                </div>
                <div className="gif star">
                    <img src="./img/icon/star.png" alt="별"></img>
                    <img src="./img/icon/about-mimo2.png" className="mimo" alt="미모티콘 이미지"></img>
                    <img src="./img/icon/star.png" alt="별"></img>
                </div>
            </div>

            <svg class="arrow-down">
                <path class="a1" d="M0 0 L15 16 L30 0"></path>
                <path class="a2" d="M0 10 L15 26 L30 10"></path>
            </svg>
        </div>
        </Slide>

        <Slide>
        <div className="line sec3">
            <div>
                <div className="text">
                    <div className="text_title">If you can not avoid it, you might as well try and enjoy it</div>
                    <div className ="text_desc">
                        안녕하세요. 웹 프론트엔드 신입 개발자 진가영입니다.<br/>
                        디자인을 공부하면서 <b style={{color:"f00"}} className="c">‘내가 만든 디자인을 사람들이 사용할 수 있게하고 싶다.’라고 생각</b>하며 웹에 처음으로 호기심을 가지게 되었습니다. <br/><br/>
                        
                        디자인만 할 줄 알았던 저에게 UI가 멈춰있지 않고 움직이는 웹사이트에 대한 재미와 흥미가 생기게 되었고,<br/>
                        UI/UX 디자이너로 있던 직장을 퇴사 후 본격적으로 퍼블리셔로서의 공부를 준비하기 시작하였습니다. <br/> <br/>

                        하다 보니 욕심이 생겨 Web Publisher보다는 더 많은 것을 해보고 싶어 <b>Front-End로 폭을 넓혀 공부를 하기 시작</b>하였습니다. <br/> <br/>

                        <b className="highlight">'디자인 출신 개발자'라는 말보다 '디자인도 가능한 Front-End 개발자'</b>가 되기 위해 정보처리기능사 실기를 준비중에 있으며,<br/> 
                        부족한 부분인 React JS 나 VueJS 같은 Front-End Framework를 꾸준히 공부하면서 항상 노력하는 개발자가 되겠습니다.
                    </div>
                </div>
                <div className="skill">
                    <ul>
                        <li>
                            <p>skill</p>
                            Illustrator, Photoshop, SolidWorks, UG, Keyshot, Excel, Powerpoint, XD
                        </li>

                        <li>
                            <p>tool</p>
                            Html, css, javascript, webpack, json, Jquery, React JS, Scss
                        </li>
                    </ul>
                </div>
                <div className="skill_icons">
                </div>
                <a href="robots.txt" download>
                    <a href="https://drive.google.com/file/d/12XnpqEMhNd7EXJIFbCVM5x_LfxKzEg9f/view?usp=sharing" target="_blank"><div className="about-resume">
                        이력서 다운로드
                    </div></a>
                </a>
            </div>
        </div>
        </Slide>

        </FullPage>
        </div>
    )
}