import React, {useEffect, useState} from 'react';
import '../App.css';

import Bg from '../comp/Bg';
import Header from '../comp/Header';

export default function Main(){

    //오전+오후
    let [hours,setHours] = useState();
    function headProps(st){
        setHours(st);
    }

    const [mobileSize, setMobileSize] = useState(false);
    
    const mobileResize = ()=>{

        if(window.innerWidth < 1200){
            setMobileSize(true);
        }else{
            setMobileSize(false);
        }
    }

    useEffect(()=>{
        window.addEventListener('load',mobileResize);
        window.addEventListener('resize',mobileResize);
        return()=>{
            window.removeEventListener('resize',mobileResize);
        }
    },[]);

    return(
        <div>
        <Bg a={headProps}/>
        <Header/>

        <div className="main">

            <div className="main_title">
                안녕하세요<br/>
                <strong>
                    <div class="message1"> 
                        <div class="word">함께하고 싶은</div>
                        <div class="word">센스 넘치는</div>
                        <div class="word">소통하고 싶은</div>
                        <div class="word">함께하고 싶은</div>
                    </div>
                </strong>
                <strong className="develop">개발자</strong><br/>
                진가영입니다.
            </div>
        </div>

        </div>
    )
}